import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <header>
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            {/* <!-- logo begin --> */}
            <div id="logo">
              <a href="">
                <h2>YOJKAJ EVENTS</h2>
              </a>
            </div>
            {/* <!-- logo close --> */}
            {/*<!-- small button begin --> */}
            <span id="menu-btn"></span>
            {/* <!-- small button close --> */}

            <Link to="" className="btn-rsvp">
              Contact
            </Link>

            {/* <!-- mainmenu begin --> */}
            <nav>
              <ul id="mainmenu">
                <li>
                  <a href="">Home</a>
                </li>
                <li>
                  <a href="">Gallery</a>
                  <ul>
                    <li>
                      <a href="">Images</a>
                    </li>
                    <li>
                      <a href="">Videos</a>
                    </li>
                  </ul>
                </li>
                <li>
                  <a href="">About Us</a>
                </li>
                <li>
                  <a href="">Services</a>
                </li>
              </ul>
            </nav>
            {/* <!-- mainmenu close --> */}
          </div>
        </div>
      </div>
    </header>
  );
};

export default Navbar;
