import React from "react";

const HomePageSlider = () => {
  return (
    <div id="content" className="no-bottom no-top">
      {/* <!-- section begin --> */}
      <section
        id="section-hero"
        class="full-height relative text-light z1 owl-slide-wrapper"
        data-stellar-background-ratio=".2"
      >
        <div class="owl-slider-nav">
          <div class="next"></div>
          <div class="prev"></div>
        </div>

        <div
          class="center-y fadeScroll text-center relative"
          data-scroll-speed="4"
        >
          <div class="container">
            <div class="row">
              <div class="col-md-8 col-md-offset-2">
                <h1 class="s2">YOJKAJ EVENTS</h1>
                <h4 class="s3">Wedding Planner</h4>
                <div class="spacer-double"></div>
              </div>
            </div>
          </div>
        </div>

        <div id="custom-owl-slider" class="owl-slide" data-scroll-speed="2">
          <div class="item">
            <img src="assets/images/slider/6.jpg" alt="" />
          </div>
          <div class="item">
            <img src="assets/images/slider/4.jpg" alt="" />
          </div>
          <div class="item">
            <img src="assets/images/slider/1.jpg" alt="" />
          </div>
        </div>
      </section>
      {/* <!-- section close --> */}
    </div>
  );
};

export default HomePageSlider;
